.lines-burger {
  margin: 15px 0 0 50px;
  height: 60px;
  width: 75px;
  position: absolute;
  top: 0;
  color: white;
  z-index: 1;
  visibility: hidden;
}

.btn1 {
  color: rgb(229, 238, 238);
}

.lines {
  display: block;
  float: left;
  clear: both;
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: 10px;
  margin-left: -30px;
}

.lines,
.lines::before,
.lines::after {
  height: 2px;
  width: 23px;
  background-color: rgb(238, 229, 229);
}

.lines::before,
.lines::after {
  content: "";
  position: absolute;
  left: 0;
}

.lines::before {
  bottom: 6px;
}

.lines::after {
  top: 6px;
}

.open1 .lines {
  transform: rotate(405deg) translate(6px);
  transition: transform 250ms ease-in-out;
  background-color: red;
}

.open1 .lines::before {
  opacity: 0;
  transition: transform 250ms, opacity 250ms ease;
}

.open1 .lines::after {
  transform: rotate(450deg) translate(-6px);
  transition: transform 250ms ease-in-out;
  background-color: red;
}

.open1 .nav-container {
  position: relative;
  top: 40px;
  width: 100%;
  z-index: 3;
  opacity: 1;
  transform: translateY(0);
  transition: transform 100ms, opacity 100ms ease;
}

.open1 .nav-wrapper {
  width: 100%;
  transition: transform 350ms ease-in;
  background-color: rgb(237, 232, 241);
}

.open1 .btn1 {
  color: rgb(156, 117, 22);
  transition: transform color 350ms ease-in;
}

