@use "../partials/" as vars;

.services-container {
	width: 100%;
	height: auto;
	padding-top: 30px;
	//background-color: $clr-lightgrey;
	display: flex;
	align-content: center;
	justify-content: center;
}

.services-wrapper {
	padding-top: 25px;
	margin-top: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 0 auto;
	background-color: vars.$clr-body;
	color: vars.$clr-imgbkcolor;
}

.services-head {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding-bottom: 20px;
}

.services-title {
	padding-bottom: 25px;
}

.services-contract {
	background-color: vars.$clr-pill;
	//width: 40%;
	padding: 20px;
	border-radius: 4px;
}

.services-sub-title {
	font-size: 18px;
	display: flex;
	align-content: center;

	justify-content: space-between;
}

.contract {
	width: 55px;
	height: 55px;
	padding-right: 15px;
	//display: inline;
}

.services-list {
	display: flex;
	//	flex-direction: column;
	flex-wrap: wrap;
	//	align-items: center;
	//align-content: center;
	//justify-items: center;
	//justify-content: center;
	width: 100%;
	padding: 15px;
	//background-color: colors.$clr-body;
}

.services-card {
	width: 33%;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
}

.services-card h3 {
	text-align: center;
	padding: 15px;
}

.calibration-svg {
	margin-top: 20px;
	padding: 20px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
}

.calibration {
	width: 100px;
	height: 100px;
	padding: 20px;
}

.services-additional-title {
	padding: 30px;
}

.services-additional {
	width: 100%;
	display: flex;
	//flex-direction: column;
	flex-wrap: wrap;

	align-items: center;
	justify-content: center;
}

.services-additional-wrapper {
	width: 50%;
	display: flex;

	align-items: center;
	justify-content: space-evenly;
}

.services-ul-item {
	width: 80%;
	padding: 20px;
	border-radius: 5px;

	background-color: vars.$clr-pill;
}

.services-sub-ul-item {
	margin-left: 25px;
}

.li-item {
	list-style-type: none;
	padding: 5px;
}

.services-end {
	text-align: center;
	margin-top: 15px;
	padding: 20px;
}

.bang-area {
	padding: 15px;
}

.bang {
	border-radius: 50%;
	padding: 5px;
}
