@use "../partials/" as vars;

.foot {
	grid-area: footer;
	width: 95vw;
	height: auto;
	color: vars.$clr-text;
	display: flex;
	flex-wrap: wrap;
	align-content: center;
	justify-content: center;
}

.foot-wrapper {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	align-content: center;
	justify-content: space-evenly;
}

.locale {
	width: 45%;
	padding: 15px;
}

.hours-container {
	width: 25%;
	padding: 15px;
}

.social {
	width: 15%;
	padding: 15px;
}

.f-aitem {
	text-decoration: none;
	color: vars.$clr-text;
	padding: 0.5rem;
}

.f-li-items {
	list-style-type: none;
	display: block;
}

.cpright {
	width: 100%;
}

.foot-bbb-img {
	max-width: 252px;
	max-height: 200px;
}
