@use "../partials/" as vars;

.main-section {
	width: 95vw;
	height: 50vh;
	background-color: aliceblue;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-auto-flow: row;
}

.hero {
	grid-column: span 2;
	width: 100%;
	// display: flex;
	// align-content: center;
	// justify-content: center;
	margin: 20px 0 20px 0;
	background-color: vars.$clr-body;
}

.hero-wrapper {
	grid-column: span 1;
	width: 100%;
	height: max-content;
	// display: flex;
	// flex-wrap: wrap;
	// align-items: center;
	// justify-content: center;
	background-color: vars.$clr-text;
}

.hero-title {
	margin: 1.25rem 0 0 0;
}

.hero-head {
	width: 100%;
	height: max-content;
	display: flex;
}

.hero-img {
	width: max-content;
	height: max-content;
	margin: 0 0 20px 20px;
	border-radius: 8px;
	background-color: vars.$clr-nav;
}

.hero-text {
	width: 60%;
	margin-left: 50px;
	border-radius: 5px;
	padding: 10px;
}
