@use "../partials/" as vars;
@use "../mixins/" as mix;

.lines-burger {
	margin: 15px 0 0 50px;
	height: 60px;
	width: 75px;
	position: absolute;
	top: 0;
	color: white;
	z-index: 1;
	visibility: hidden;
}

.btn1 {
	// width: 60px;
	// height: 30px;
	color: rgb(229, 238, 238);

	//background-color: rgb(14, 13, 13);
}

.lines {
	display: block;
	float: left;
	clear: both;
	position: absolute;
	top: 0;
	bottom: 0;
	margin-top: 10px;
	margin-left: -30px;
}

.lines,
.lines::before,
.lines::after {
	height: 2px;
	width: 23px;
	background-color: rgb(238, 229, 229);
}

.lines::before,
.lines::after {
	content: "";
	position: absolute;
	left: 0;
}

.lines::before {
	bottom: 6px;
}

.lines::after {
	top: 6px;
}

.open1 .lines {
	@include mix.rotate-line();
}

.open1 .lines::before {
	@include mix.rotate-lines-before();
}

.open1 .lines::after {
	@include mix.rotate-lines-after();
}

.open1 .nav-container {
	@include mix.btn-nav-con();
}

.open1 .nav-wrapper {
	@include mix.btn-nav-wrap();
}

.open1 .btn1 {
	@include mix.button1();
}
