@use "../partials/" as vars;

.card-section {
	width: 100%;
	height: calc(100vh - 100px);
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 1fr;
	// display: flex;
	// flex-direction: column;
	// align-items: center;
	background-color: vars.$clr-nav;
	padding-bottom: 25px;
	//flex: 1 1 100%;
}

.card-section-header {
	grid-column: span 3;
	width: 100%;
	height: 300px;
	margin-top: 25px;
	// display: flex;
	// flex-direction: column;
	// align-items: center;
	// align-content: center;
	// justify-content: center;
	background-color: vars.$clr-lightgrey;
}

.card-section-title {
	margin: 25px 0 25px 0;
}

.card-section-body {
	width: 60%;
	height: 150px;
	margin: 10px 0 10px 0;
	align-items: center;
	background-color: vars.$clr-nav;
	// display: flex;
	// flex-direction: column;
	// gap: 15px;
	// justify-content: center;
	border-radius: 10px;
}

.card-containers {
	width: 100%;
	height: 450px;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(300px, 325px));
	background-color: vars.$clr-aactive;
	align-self: center;
	justify-self: stretch;
	gap: 15px;
	// display: flex;
	// //	flex-direction: column;
	// flex-wrap: wrap;
	// justify-content: space-evenly;
	margin-top: 3.5rem;
	//	flex: 0 0 25%;
}

.cards {
	grid-column: span 1;
	max-width: 300px;
	max-height: 275px;
	// display: flex;

	// flex-wrap: wrap;
	// align-items: center;
	// align-content: center;
	// justify-content: center;
	// justify-items: center;
	// flex: 1 1 33%;
	background-color: vars.$clr-text;
}

.card {
	width: 100%;
	max-height: max-content;
	background-color: vars.$clr-body;
	// display: flex;
	// flex-wrap: wrap;
	// flex-direction: column;
	text-align: center;
}

.card-text {
	color: vars.$clr-text;
}

.card-img {
	background-color: vars.$clr-text;
	justify-content: center;
}

.img-card {
	width: 150px;
	height: 150px;
}
