@use "colors";

body,
div,
aside,
main,
nav,
header,
section,
footer,
input,
label,
textarea,
p,
ul,
li,
a,
span,
::before,
::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: "IBM Plex Sans", sans-serif;
	line-height: 1.45;
	font-weight: 600;
	letter-spacing: 0.18rem;
	font-size: 16px;
	font-size: clamp(0.875rem, 0.85rem + 0.125vw, 1rem);
}

// reset button defaults
button {
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}
