@use "../partials/" as vars;

.work {
	width: 100%;
	height: calc(100vh - 100px);
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(375px, 400px));
	gap: 15px;

	background-color: vars.$clr-imgbkcolor;
	padding-bottom: 20px;
}

.work-list {
	grid-column: span 1;
	margin: 50px 0 50px 0;
	width: 100%;
	max-height: max-content;
	margin: 0 auto;
	//display: flex;
	//flex-wrap: wrap;
}

.work-head {
	width: 100%;
	height: max-content;
	/* display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center; */
}

.work-title {
	width: 100%;
	height: max-content;
	text-align: center;
	border: 1px solid black;
	border-radius: 5px;
	padding: 10px;
	margin-bottom: 20px;
	background-color: vars.$clr-pill;
}

.cert {
	max-width: max-content;
	max-height: max-content;
	padding: 13px 0 30px 0;
}

.price {
	max-width: max-content;
	max-height: max-content;
}

.work-content {
	//color: vars.$clr-text;
	//background-color: vars.$clr-body;
	width: 100%;
	max-height: min-content;
	padding: 10px;
}
