@use "../partials/" as vars;

.task {
	width: 100%;
	height: max-content;
	display: flex;
	flex-direction: column;
	align-content: center;
	justify-content: center;
	color: vars.$clr-pill;
}

.task-wrapper {
	width: 100%;
	height: max-content;
	display: flex;
	flex-direction: column;
	align-items: center;
	align-content: center;
	justify-items: center;
	justify-content: center;
}

.task-title {
	text-align: center;
	width: 100%;
	margin: 30px 0 30px 0;
}

.task-text {
	padding: 15px;
}

.task-head {
	width: 100%;
	height: max-content;
	display: flex;
	flex-wrap: wrap;
	padding: 15px;
	justify-items: center;
}
/* 
.task-img-container {
	width: 100%;
	height: max-content;
	display: flex;
	justify-content: space-evenly;
	padding: 20px 0 20px 0;
} */

.task-img {
	margin: 0 auto;
	width: max-content;
	border-radius: 12px;
	padding: 15px;
	background-color: vars.$clr-nav;
}

.task-ul-container {
	width: 100%;
	height: max-content;

	//justify-items: center;
	display: flex;
	//justify-items: center;
	//	flex-flow: column wrap;
	// flex-direction: row;
	// flex-wrap: wrap;

	padding: 25px;
}

.task-ul-wrapper {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 20px;
	width: 100%;
	height: max-content;
	align-self: center;
	display: flex;
	flex-wrap: wrap;
}

.task-li-wrapper {
	grid-column: span 1;
	width: 100%;
	height: max-content;
	text-align: center;
	/* display: flex;
	flex-direction: column; */
}

.task-li-items {
	width: 100%;
	list-style-type: none;
	padding: 8px;
}
