@use "../partials/" as vars;

html {
	scroll-behavior: smooth;
}

body {
	width: 100vw;
	height: 100vh;
	//background-color: vars.$clr-ahover;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-areas:
		"page page page page"
		"nav nav nav nav "
		"header header header header"
		"main main main main"
		"footer footer footer footer";
	position: relative;

