@use "../partials/" as vars;

.form-container {
	width: 100%;
	height: 100%;
	background-color: vars.$clr-pill;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.name-tag-container {
	margin: 4rem 0 4rem 0;
	width: 50%;
	height: 15rem;
	border-radius: 10px;
	z-index: 2;
	background-color: vars.$clr-body;
}

.name-tag-title {
	color: vars.$clr-text;
	text-align: center;
	padding: 2px;
	font-size: 24px;
}

.name-tag {
	margin: 0 auto;
	width: 70%;
	height: 8rem;
	background-color: vars.$clr-imgbkcolor;
	position: relative;
	z-index: -1;
	top: 1rem;
	display: flex;
	justify-content: space-evenly;
	font-size: 18px;
	font-family: "Covered By Your Grace", cursive;
}

.form-wrapper {
	width: 80%;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	align-items: center;
	align-content: center;
	justify-items: center;
	justify-content: center;
	border-radius: 0.5rem;
	margin: 0 0 4rem 0;
}

.f-contact {
	width: 60%;
	display: flex;
	flex-direction: column;
	align-content: center;
	justify-content: center;
}

.form-item {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-content: center;
	justify-content: center;
	padding: 25px;
}

.f-label {
	padding: 5px;
	text-transform: uppercase;
	border-radius: 5px 5px 0 0;
	color: vars.$clr-lines;
}

.f-input {
	height: 30px;
	outline: none;
	appearance: none;
	border: none;
	background-color: vars.$clr-imgbkcolor;
	transition: 0.5s ease;
	border-radius: 4px;
}

.f-input:focus,
textarea:focus {
	border: 1px solid vars.$clr-body;
	background-color: vars.$clr-lightgrey;
}

.form-error {
	padding: 0.3rem;
	color: vars.$clr-lines;
}

#message {
	outline: none;
	appearance: none;
	border: none;
}

.messagearea {
	outline: none;
	appearance: none;
	border: none;
	resize: none;
	background-color: vars.$clr-imgbkcolor;
	transition: 0.5s ease;
	border-radius: 4px;
}

.btn-contact {
	padding: 15px;
	text-transform: uppercase;
	border: 1px solid black;
	border-radius: 10px 0 10px 0;
	margin: 20px 0 20px 25px;
	background-color: vars.$clr-button;
	color: vars.$clr-text;
}
